@font-face {
  font-family: 'main'; /*a name to be used later*/
  src: url('./Bebas-Regular.ttf'); /*URL to font*/
}

* {
  margin: 0px;
}

.App {
  text-align: center;
}

.App-logo {
  position: fixed;
  height: 100%;
  left: -10px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

a:visited {
  color: white;
}
a {
  text-decoration: none;
}

.info {
  position: fixed;
  left: 68%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
  color: white;
  font-family: main;
  font-size: 110px;
  display: inline-block;
}

.meetLink {
  font-size: 50px;
  padding: 20px;
  background-color: #1e2127;
  border-radius: 10px;
  box-shadow: 5px black;
}
.meetLink:hover {
  padding: 21px;
  font-size: 51px;
}